import { Message, MessageBox } from 'element-ui'

let messageFunction = {
    me,   //错误(MessageError)
    mi,    //取消(MessageInfo)
    ms, //成功(MessageSuccess)
    mw, //警告(MessageWarning)
    mbs   //多选(MessageBoxSuccess)
}

export default messageFunction

export function me(text = '错误') {
    Message({
        message: text,
        type: 'error',
        duration: 3 * 1000
    });
}
export function mi(text = '取消') {
    Message({
        message: text,
        type: 'info',
        duration: 3 * 1000
    });
}
export function ms(text = '成功') {
    Message({
        message: text,
        type: 'success',
        duration: 3 * 1000
    });
}

export function mw(text = '警告') {
    Message({
        message: text,
        type: 'warning',
        duration: 3 * 1000
    });
}

export function mbs(text1 = '弹框提示信息', text2 = '成功消息提示', text3 = '取消消息提示', others) {
    return MessageBox.confirm(text1, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
    }).then(() => {
        Message({
            message: text2,
            type: 'success',
            showClose: true,
            duration: 3 * 1000
        });
    }).catch(() => {
        Message({
            message: text3,
            type: 'warning',
            showClose: true,
            duration: 3 * 1000
        });
    })
}

