import mockApi from '@/reques/apiClass/mockApi'; // 导入模拟接口域名列表
import api from '@/reques/apiClass/api'; // 导入接口域名列表
import { get,post } from '@/reques/apiClass/http'; // 导入axios实例

const Mock = g => get(`${mockApi.Mock}`, g);
const www = g => get(`${mockApi.www}`, g);

const Login = g => get(`${api.Login}`, g);
const Register = g => get(`${api.Register}`, g);
const User = g => get(`${api.User}`, g);
const SearchB = g => get(`${api.SearchB}`, g);
const BookList = g => get(`${api.BookList}`, g);
const TeaList = g => get(`${api.TeaList}`, g);
const TeaBlist = g => get(`${api.TeaBlist}`, g);
const AddUser = g => get(`${api.AddUser}`, g);
const ModifyUser = g => get(`${api.ModifyUser}`, g);
const DeleteUser = g => get(`${api.DeleteUser}`, g);
const AddBook = g => get(`${api.AddBook}`, g);
const ModifyBook = g => get(`${api.ModifyBook}`, g);
const DeleteBook = g => get(`${api.DeleteBook}`, g);

export default {
    Mock,//本地模拟数据
    www,//在线免费接口

    Login,//登录请求
    Register,//注册请求
    User, //用户信息请求
    SearchB,//图书信息请求
    BookList,//按照图书查询请求
    TeaList,//按照教师查询请求
    TeaBlist,//提交教师所选书籍列表
    AddUser,//添加用户
    ModifyUser,//修改用户信息
    DeleteUser,//删除用户
    AddBook,    //添加图书
    ModifyBook, //修改图书信息
    DeleteBook  //删除图书
}
