import Vue from 'vue'
import VueRouter from 'vue-router'

const Login =  () => import(/* webpackChunkName: 'ImportFuncDemo' */ '@/components/login')
const Home =  () => import(/* webpackChunkName: 'ImportFuncDemo' */ '@/components/home')
const Welcome =  () => import(/* webpackChunkName: 'ImportFuncDemo' */ '@/components/welcome')
const User =  () => import(/* webpackChunkName: 'ImportFuncDemo' */ '@/view/user/user')
const searchb =  () => import(/* webpackChunkName: 'ImportFuncDemo' */ '@/view/book/searchb')
const searchbtea =  () => import(/* webpackChunkName: 'ImportFuncDemo' */ '@/view/book/teaList')
const searchbook =  () => import(/* webpackChunkName: 'ImportFuncDemo' */ '@/view/book/booksList')
Vue.use(VueRouter)

const routes = [
  { path:'/',redirect:'/Login'  },
  { path:'/Login',component:Login },  
  { path:'/Home',
    component:Home,
    redirect:'/Welcome',
    children:[
      {path:'/Welcome',component:Welcome}
      ,{path:'/userList',component:User}
      ,{path:'/bookList',component:searchb}
      ,{path:'/teaList',component:searchbtea}
      ,{path:'/booksList',component:searchbook}
  ]
  }
]

const router = new VueRouter({
  routes
})

export default router
